import React, { useState } from 'react';
import styles from './Instructor_registration.module.css';

const Instructor_registration = ({ onClose }) => {
  const [registerForm, setRegisterForm] = useState({
    name: "",
    tel: "",
    course: "",
    upfiles: "null",
  });


  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setRegisterForm((prevForm) => ({
      ...prevForm,
      [name]: value,
    }));
  };

  //      .
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    setRegisterForm((prevForm) => ({
      ...prevForm,
      upfiles: file,
    }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    console.log(registerForm);

    try {
      const formData = new FormData();
      formData.append('name', registerForm.name);
      formData.append('tel', registerForm.tel);
      formData.append('course', registerForm.course);
      formData.append('upfiles', registerForm.upfiles);

      const response = await fetch('http://localhost:8888',
        {
          method: 'POST',
          body: formData,
        });

      if (response.ok) {
        const data = await response.json();
        console.log(' :', data);
        alert('  .');
        onClose();
      } else {
        console.error(' :', response.statusText);
        alert('  .');
      }
    } catch (error) {
      console.error('   :', error);
      alert(' .  .');
    }
  };

  const handleCancelClick = () => {
    onClose();
  };

  return (
    <>
      <div className={styles.registration_background}>
        <div className={styles.registration_container}>
          <h2> </h2>
          <form onSubmit={handleSubmit}>
            <div className={styles.registration_form}>
              <input
                type="text"
                className={styles.registration_textbox}
                placeholder=""
                name="name"
                value={registerForm.name}
                onChange={handleInputChange}
              />
              <input
                type="tel"
                className={styles.registration_textbox}
                placeholder="(-)"
                name="tel"
                value={registerForm.tel}
                onChange={handleInputChange}
                maxLength={11}
              />
              <input
                type="text"
                className={styles.registration_textbox}
                placeholder=""
                name="course"
                value={registerForm.course}
                onChange={handleInputChange}
              />
              <div className={styles.registration_photo}>
                <input
                  type="file"
                  accept="image/*"
                  name="upfiles"
                  onChange={handleFileChange}
                />
              </div>
              <div className={styles.registration_btns}>
                <button type="submit" className={styles.registration_submit} onClick={handleSubmit}></button>
                <button type="button" className={styles.registration_cancel} onClick={handleCancelClick}></button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </>
  );
};

export default Instructor_registration;