import React from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './Trainee_deletion.module.css';
import { Link } from 'react-router-dom';

const Trainee_deletion = ({ traineeId, onClose, updateMembers }) => {
  const navigate = useNavigate();
  
  const handleDeleteClick = () => {
    navigate('/trainee_list');
  }

  const handleDeleteTrainee = async () => {
    try {
      const response = await fetch('https://localhost:443/trainee/delete', {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ id: traineeId }), //  ID 
      });

      if (response.ok) {
        const data = await response.json();
        console.log(' :', data);
        alert('  .');
        updateMembers(traineeId); //    
      } else {
        console.error(' :', response.statusText);
        alert('  .');
      }
    } catch (error) {
      console.error('   :', error);
      alert(' .  .');
    }
  };

  return (
    <>
      <div className={styles.delete_background}>
        <div className={styles.delete_container}>
          <h2>   ?</h2>
          <div className={styles.delete_btns}>
            <button onClick={handleDeleteTrainee} className={styles.delete_submit}></button>
            <button type="button" className={styles.delete_cancel} onClick={handleDeleteClick}></button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Trainee_deletion;
