import React, { useState } from 'react';

import styles from './Login.module.css';



const Login = () => {
    console.log("Login() invoked.");


    //  :    
    const [loginData, setLoginData] = useState({
        userId: '',
        passwd: ''
    });

    const [errorMessage, setErrorMessage] = useState(' ');  //     


    //   
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setLoginData((prevData) => ({
            ...prevData,
            [name]: value
        }));
    };

    //        
    const handleLogin = async () => {
        const { userId, passwd } = loginData;

        //   
        try {
            const response = await fetch('https://localhost:443/login/', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ userId, passwd }) // JSON   
            });

            const data = await response.json();     //   JSON  

            if (response.ok) {
                //    
                console.log(" ");
                setErrorMessage('');                    //     
            } else {
                //    
                console.log(" ");
                setErrorMessage(data);                  //    
            }
        } catch (error) {
            console.error(" :", error);
            setErrorMessage("   ."); //      
        }
    };


    return (
        <div className={styles.login}>
                <div className={styles.logo}></div>
            <div className={styles.main}>

                <form>
                    <input
                        className={styles.id}
                        placeholder="userId"
                        name="userId"                           //   
                        value={loginData.userId}
                        onChange={handleInputChange}
                    />
                    <i className={`fas fa-user ${styles.user_icon}`} />
                    <input
                        className={styles.pw}
                        placeholder="password"
                        name="passwd"                     //   
                        type="password"
                        value={loginData.passwd}
                        onChange={handleInputChange}
                    />
                    <i className={`fas fa-lock ${styles.pass_icon}`} />

                    <div className={styles.errortext}>{errorMessage}</div>
                </form>

                    <button className={styles.loginbutton} onClick={handleLogin}>
                        
                    </button>
            </div>
        </div>
    )
}

export default Login;