import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";


import styles from './Course_check.module.css';  
import Course_info from './Course_info';
import Course_content from './Course_content';
import Course_delete from './Course_delete';
import {Member}  from '../member_list';
import { useParams } from "react-router-dom";

const Course_check = () => {
    console.log("Course_check() invoked.");

    const navigate = useNavigate();
    //    ///////////////////////////////////////////////////////////////////////////////////////
    const {courseId} = useParams(); //   

    const [formData, setFormData] = useState({
        type: "",
        name: "",
        instructor: "",
        startDate: "",
        endDate: "",
        capacity: "",
        currCount: "",
        detail: ""
    });

   
    useEffect(() => {                               //    (   )
        console.log("courseId:", courseId);
        fetch(`https://localhost:443/course/${courseId}`)
            .then((response) => {
                if (!response.ok) {
                    throw new Error("  ");
                }
                return response.json();
            })
            .then((data) => {
                setFormData({
                    // upfiles: data?.upfiles || [],
                    type: data?.type || "",
                    name: data?.name || "",
                    instructor: data?.instructor || "",
                    startDate: data?.startDate || "",
                    endDate: data?.endDate || "",
                    currCount: data?.currCount || "",
                    capacity: data?.capacity || "",
                    detail: data.detail || "",
                });
            })
            .catch((error) => {
                console.error("  :", error);
            });
            // fetchData();    // *** db   ***! 
    }, []);

    ////  ////////////////////////////////////////////////////////////////////////////////////////////////
      
    const handleModifyShowPopup = () => {
        navigate(`/course/update/${courseId}`);
    };
    

    ////  ////////////////////////////////////////////////////////////////////////////////////////////////

    const [showDeletePopup, setShowDeletePopup] = useState(false);  //  


    //      
    const handleDeleteShowPopup = () => {
        setShowDeletePopup(true);
    };

    //  
    const handleDeleteClosePopup = () => {
        setShowDeletePopup(false);
    };

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    
   
    return(

        <div className={styles.main}>
        
            {showDeletePopup && <Course_delete onClose={handleDeleteClosePopup}/>}

            <div className={styles.topbox}>

                <div className={styles.top_leftbox}>

                     <button className={styles.backbutton} onClick={()=> navigate("/course_overview")}><i class="fa-solid fa-chevron-left fa-1.8x"></i></button> {/*   */}
                     <div className={styles.imgbox}></div>
                     <div className={styles.buttonbox}>      
                         <button className={styles.modify_button} onClick={handleModifyShowPopup}></button>
                         <button className={styles.delete_button} onClick={handleDeleteShowPopup}></button> 
                     </div>

                </div>

                <div className={styles.top_rightbox}>
                    <div className={styles.inputboxes}>
                        <Course_info label="" value={formData.type} disabled/>
                        <Course_info label="" value={formData.startDate} disabled/>
                        <Course_info label="" value={formData.name} disabled/>
                        <Course_info label="" value={formData.endDate} disabled/>
                        <Course_info label="" value={formData.instructor} disabled/>
                        <Course_info  label={` (: ${formData.currCount})`} value={formData.capacity} disabled/>
                    </div>
                    <Course_content label="" value={formData.detail} disabled/>
                </div>
                

            </div>

            <div className={styles.bottombox}>
                {/* <Course_memberlist/> */}
                {/* <Member /> */}
            </div>

        </div>
    )

}


export default Course_check;