import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import styles from './Trainee_preview.module.css';
import { Trainee_Registration } from '..';
import img1 from '../img/1.png';
import img2 from '../img/2.png';
import img3 from '../img/3.png';
import img4 from '../img/4.png';
import img5 from '../img/5.png';
import img6 from '../img/6.png';
import img7 from '../img/7.png';


const Trainee_list = () => {

  const criteriaDTO = {
    page: 0,
    pageSize: 10,
    condition: "name",
    q: ""
  };
  
  const [members, setMembers] = useState([ //   (  ?  ?)
    {
      id: 'A0123456',
      name: '',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.06',
      status: '',
      photo: img1,
    },
    {
      id: 'A5431823',
      name: '',
      phone: '010-4167-5138',
      course: '',
      date: '2025.02.23',
      status: '',
      photo: img2,
    },
    {
      id: 'A4315749',
      name: '',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.06',
      status: '',

      photo: img3,
    },
    {
      id: 'A85612654',
      name: '',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.15',
      status: '',
      photo: img4,
    },
    {
      id: 'A984651',
      name: '',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.01',
      status: '',
      photo: img5,
    },
    {
      id: 'A534651',
      name: '',
      phone: '010-1234-5678',
      course: '',
      date: '2025.01.18',
      status: '',
      photo: img6,
    },
    {
      id: 'A766213',
      name: '',
      phone: '010-1234-5678',
      course: '',
      date: '2025.01.18',
      status: '',
      photo: img7,
    },
    {
      id: 'A6873513',
      name: '1',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.06',
      status: '',
      photo: '',
    },
    {
      id: 'A572145',
      name: '2',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.06',
      status: '',
      photo: '',
    },
    {
      id: 'A6721346',
      name: '3',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.06',
      status: '',
      photo: '',
    },
    {
      id: 'A11223344',
      name: '4',
      phone: '010-1234-5678',
      course: '',
      date: '2025.03.06',
      status: '',
      photo: '',
    },
  ]);

  // //   
  // const statusOptions = [
  //   { value: "1", label: "" },
  //   { value: "2", label: "" },
  //   { value: "3", label: "" },
  //   { value: "4", label: "" },
  // ];

  const handleRegistrationClick = () => {
    setShowRegistration(true);
  };

  const handleCloseRegistration = () => {
    setShowRegistration(false);
  };

  useEffect(() => {   //*** db   ***
    const fetchData = async () => {
      try {
        const response = await fetch('https://localhost:443/trainee/list');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        setMembers(data);
        setFilteredMembers(data);
      } catch (error) {
        console.error('Error fetching data:', error.message);
      }
    };
    fetchData();
  }, []);

  // useEffect(() => {
  //   const fetchData = async () => {
  //     const r1 = { register: "R1" };

  //     //   
  //     try {
  //       const response = await fetch('https://localhost:443/trainee/list', {
  //         method: 'POST',
  //         headers: {
  //           'Content-Type': 'application/json'
  //         },
  //         body: JSON.stringify(r1) // JSON   
  //       });

  //       const data = await response.json();     //   JSON  

  //       if (response.ok) {
  //         //    
  //         console.log("~ (trainee/list)");
  //         setErrorMessage('');                    //     
  //       } else {
  //         //    
  //         console.log(" ");
  //         setErrorMessage(data);                  //    
  //       }
  //     } catch (error) {
  //       console.error(" :", error);
  //       setErrorMessage("   ."); //      
  //     }
  //   };
  // }, []);

  //   
  const [searchTerm, setSearchTerm] = useState(''); // 
  const [searchOption, setSearchOption] = useState(''); //  
  const [selectedStatus, setSelectedStatus] = useState([]); //  
  const [filteredMembers, setFilteredMembers] = useState(members);  //  
  const [showRegistration, setShowRegistration] = useState(false);
  const [showModification, setShowModification] = useState(false);

  //   
  const [visibleMembers, setVisibleMembers] = useState(members.slice(0, 3)); //    
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true); //     
  const observerRef = useRef(null); // IntersectionObserver  ref

  //  /   
  const [showEditMenu, setShowEditMenu] = useState(null);
  const editMenuRef = useRef(null);

  //  /    
  const handleEditButton = (index) => {
    if (showEditMenu === index) {
      setShowEditMenu(null); //    
    } else {
      setShowEditMenu(index); //  
      setShowModification(false); //   
    }
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (editMenuRef.current && !editMenuRef.current.contains(event.target)) {
        setShowEditMenu(null); //    ,    
      }
    };

    document.addEventListener('mousedown', handleClickOutside);

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showEditMenu]);

  //     
  const handleSearch = () => {
    if (searchOption === 'name') {
      const searchedMembers = members.filter((member) => member.name.includes(searchTerm));
      setFilteredMembers(searchedMembers);
      setVisibleMembers(searchedMembers);
      setHasMore(true);
    } else if (searchOption === 'username') {
      const searchedMembers = members.filter((member) => member.username.includes(searchTerm));
      setFilteredMembers(searchedMembers);
      setVisibleMembers(searchedMembers);
      setHasMore(true);
    } else if (searchOption === 'phone') {
      const searchedMembers = members.filter((member) => member.phone.includes(searchTerm));
      setFilteredMembers(searchedMembers);
      setVisibleMembers(searchedMembers);
      setHasMore(true);
    } else {
      const searchedMembers = members.filter((member) => (member.name.includes(searchTerm) || member.username.includes(searchTerm)));
      setFilteredMembers(searchedMembers);
      setVisibleMembers(searchedMembers);
      setHasMore(true);
    }
  };

  //     
  const handleStatusChange = (e) => {
    const { value, checked } = e.target;
    if (checked) {
      setSelectedStatus((prev) => [...prev, value]);
      // setSelectedStatus(value);
    } else {
      setSelectedStatus((prev) => prev.filter((status) => status !== value));
    }
    filterByStatus();
  };

  //   
  const filterByStatus = () => {
    if (selectedStatus.length === 0) {
      setFilteredMembers(members);
    } else {
      const statusMap = {
        "1": "",
        "2": "",
        "3": "",
        "4": "",
      };

      const filteredMembersByStatus = members.filter((member) => {
        return selectedStatus.some((status) => member.status === statusMap[status]);
      });

      setFilteredMembers(filteredMembersByStatus);
    }
    setVisibleMembers(filteredMembers); //     
    // setHasMore(true); //      
  };

  //     
  const getCourseColor = (course) => {
    switch (course) { //   (***)
      case "":
        return "#FFEB9B";
      case "":
        return "#72F2F6";
      case "":
        return "#96FF88";
      default:
        return "#ddd"; //  
    }
  };

  //      
  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleSearch();
    }
  };

  //      
  const loadMoreMembers = () => {
    setLoading(true);
    setTimeout(() => {
      const nextIndex = visibleMembers.length;
      const moreMembers = filteredMembers.slice(nextIndex, nextIndex + 5); // filteredMembers   

      if (moreMembers.length > 0) {
        setVisibleMembers((prev) => [...prev, ...moreMembers]);
      } else {
        setHasMore(false); //     hasMore false 
      }

      setLoading(false);
    }, 1000);
  };

  //   
  useEffect(() => {
    if (loading || !hasMore) return;

    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          loadMoreMembers();
        }
      },
      { threshold: 0.1 }
    );

    if (observerRef.current) observer.observe(observerRef.current);

    return () => observer.disconnect();
  }, [loading, hasMore, filteredMembers]);

  const [errorMessage, setErrorMessage] = useState(' ');  //     

  return (
    <div className={styles.main}>
      <div className={styles.title}> </div>

      <div className={styles.btns}>
        <button className={styles.registerBtn} onClick={handleRegistrationClick}> </button> {/*    */}
        {showRegistration && (
          <Trainee_Registration onClose={handleCloseRegistration} />
        )}

        {/*  */}
        <div className={styles.searchContainer}>
          
          <select
            name='status'
            className={styles.dropdown}
            value={searchOption}
            onChange={(e) => setSearchOption(e.target.value)}
          >
            <option value=""> </option>
            <option value="1"></option>
            <option value="2"></option>
            <option value="3"></option>
            <option value="4"></option>
          </select>

          <select
            name="searchWord"
            className={styles.dropdown}
            value={searchOption}
            onChange={(e) => setSearchOption(e.target.value)}
          >
            <option value=""> </option>
            <option value="name"></option>
            <option value="phone"></option>
          </select>


          <input
            name="searchText"
            type="text"
            placeholder=" "
            className={styles.input}
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onKeyUp={handleKeyPress}
          />
          <button className={styles.icon} onClick={handleSearch}>
            <i className='fas fa-magnifying-glass' />
          </button>
        </div>
      </div>


      {/*   */}
      <div className={styles.tableHeader}>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div></div>
        <div> / </div>
      </div>

      {/*   */}
      <table className={styles.tableBody}>
        <tbody>
          {visibleMembers.map((member, index) => (
            <tr key={index}>
              <td className={styles.photo} style={{ borderLeft: `10px solid ${getCourseColor(member.course)}` }}>
                <img src={member.photo} alt='member photo' />
              </td>
              <td>{member.id}</td>
              <td>{member.name}</td>
              <td>{member.phone}</td>
              <td>{member.course}</td>
              <td>{member.date}</td>
              <td>{member.status}</td>
              <td>
                <button onClick={() => handleEditButton(index)} className={`${styles.edit}`}></button>
                {showEditMenu === index && (
                  <div ref={editMenuRef} className={styles.editMenu}>
                    <Link to={'/trainee_modify'} className={styles.modify}></Link> {/*    */}
                    <Link to={'/trainee_delete'} className={styles.delete}></Link>
                  </div>
                )}
              </td> {/*  /   */}
            </tr>
          ))}
        </tbody>
      </table>

      {/*   div */}
      <div ref={observerRef} style={{ height: "3px" }} />

      {/*   */}
      {loading && <p style={{ textAlign: 'center', marginTop: '0' }}>Loading...</p>}
      {!hasMore && ''}
    </div>
  );
};

export default Trainee_list;
